import '@servicenow/now-button';
import '@servicenow/now-content-tree';
import '@servicenow/now-tabs';
import {
	CREATE_LIST,
	MENU_ITEM_CLICKED,
	MENU_ITEM_EXPANDED,
	MESSAGES,
	MODAL_TYPES,
	NOW_CONTENT_TREE_EXPANDED_ITEMS_SET,
	NOW_CONTENT_TREE_ITEM_CLICKED,
	OPEN_MODAL,
	REORDER_LIST
} from '../../constants';
import {addRetainedElement} from '../../utils/focusUtility';
import {createCustomElement} from '@servicenow/ui-core';
import {
	dirtyBehavior,
	dirtyModalInterceptor
} from '../../behaviors/dirtyBehavior/dirtyBehavior';
import {getExpandedItems} from '../listMenu/utils';
import {t} from 'sn-translate';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './listMenuDisplay.scss';

const newButtonLabel = t('New list');
const reorderButtonLabel = t('Reorder');

const view = (state, {dispatch}) => {
	const {
		expandedItems,
		hasUserCollapsedAllItems,
		properties: {
			activeTabId,
			hideListCreation,
			list,
			list: {canEdit, newListForm},
			selectedItemPath,
			tabItems
		}
	} = state;

	const expanded = list.canExpand
		? getExpandedItems({
				list,
				expandedItems,
				hasUserCollapsedAllItems
		  })
		: [];

	const canCreate = !isEmpty(get(newListForm, 'form', ''));
	const listLength = list.data.length;

	const props = {
		componentName: activeTabId,
		items: list.data,
		manageSelectedItems: true,
		select: 'single',
		selectedItems: [selectedItemPath],
		overflow: 'truncate'
	};

	const configAria = {'aria-label': newButtonLabel, 'aria-haspopup': 'true'};

	return (
		<div className={`sn-list-menu`}>
			<now-tabs
				items={tabItems}
				fixed-width
				manage-selected-item={true}
				selected-item={activeTabId}
			/>
			<div className="sn-list-menu-body">
				{list.canExpand
					? renderTree({
							...props,
							expandedItems: expanded,
							manageExpandedItems: true
					  })
					: listLength > 0
					? renderTree({...props})
					: renderEmpty(hideListCreation)}
			</div>
			{canCreate || canEdit ? (
				<div className="sn-list-menu-footer">
					{!hideListCreation && canCreate ? (
						<now-button
							label={newButtonLabel}
							variant="primary"
							icon="plus-fill"
							size="md"
							key={`newButton`}
							data-key={`newButton`}
							configAria={configAria}
							on-click={() => {
								dispatch(CREATE_LIST, {useiFrame: false});
							}}
						/>
					) : null}
					{listLength > 1 && canEdit ? (
						<now-button
							icon-name="arrow-reorder-fill"
							config-aria={{
								'aria-label': reorderButtonLabel
							}}
							variant="secondary"
							size="md"
							label={reorderButtonLabel}
							key={`reorderButton`}
							data-key={`reorderButton`}
							on-click={() => {
								dispatch(REORDER_LIST, {isEditing: true});
							}}
						/>
					) : null}
				</div>
			) : null}
		</div>
	);
};

const renderTree = props => <now-content-tree {...props} />;

const renderEmpty = () => {
	return (
		<div className="sn-empty-state">
			<div className="sn-empty-state-row -header">
				{MESSAGES.MY_LIST.EMPTY_STATE_TITLE}
			</div>
			<div className="sn-empty-state-row">
				{MESSAGES.MY_LIST.EMPTY_STATE_CONTENT}
			</div>
		</div>
	);
};

createCustomElement('sn-list-menu-display', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		hasUserCollapsedAllItems: false,
		expandedItems: []
	},
	properties: {
		activeTabId: {
			default: ''
		},
		hideListCreation: {
			default: false
		},
		instanceId: {
			default: ''
		},
		list: {
			default: {},
			schema: {
				type: 'object',
				properties: {
					title: {type: 'string'},
					key: {type: 'string'},
					canEdit: {type: 'boolean'},
					canExpand: {type: 'boolean'},
					data: {
						type: 'array',
						items: {
							type: 'object',
							properties: {
								columns: {type: 'string'},
								conditions: {},
								displayValue: {type: 'string'},
								id: {type: 'string'},
								label: {type: 'string'},
								order: {},
								selectionPath: {type: 'array'},
								table: {type: 'string'},
								targetTable: {type: 'string'}
							}
						}
					},
					newListForm: {
						type: 'object',
						properties: {
							form: {type: 'string'},
							view: {type: 'string'}
						}
					}
				}
			}
		},
		selectedItemPath: {
			schema: {
				type: 'array',
				items: {
					type: 'string'
				}
			}
		},
		tabItems: {
			default: [],
			type: 'array',
			items: {
				type: 'object',
				properties: {
					id: {type: 'string'},
					label: {type: 'string'}
				}
			}
		}
	},
	behaviors: [dirtyBehavior],
	actionHandlers: {
		[CREATE_LIST]: {
			effect: ({
				dispatch,
				action: {
					meta: {event},
					payload: {useiFrame}
				},
				properties: {
					list: {newListForm}
				}
			}) => {
				if (useiFrame) {
					addRetainedElement('newListButton', event.target);
					dispatch(OPEN_MODAL, {
						modalProps: {
							type: MODAL_TYPES.NEW_LIST,
							newListForm,
							useiFrame
						}
					});
				}
			},
			interceptors: [dirtyModalInterceptor]
		},
		[NOW_CONTENT_TREE_ITEM_CLICKED]: {
			effect: ({dispatch, action}) => {
				const item = action.payload.item;
				dispatch(MENU_ITEM_CLICKED, {item});
			},
			stopPropagation: true,
			interceptors: [dirtyModalInterceptor]
		},
		[NOW_CONTENT_TREE_EXPANDED_ITEMS_SET]: {
			effect: ({dispatch, action, updateState}) => {
				const {
					payload,
					meta: {componentName}
				} = action;
				const expandedItems = payload.value;
				updateState({expandedItems});

				if (isEmpty(expandedItems)) {
					updateState({hasUserCollapsedAllItems: true});
				} else {
					updateState({hasUserCollapsedAllItems: false});
				}
				dispatch(MENU_ITEM_EXPANDED, {
					listKey: componentName,
					items: expandedItems
				});
			},
			stopPropagation: true
		}
	},
	styles
});
