import '../listMenuDisplay/listMenuDisplay';
import '../listMenuEditor/listMenuEditor';
import '../modal/newListModal';
import '@servicenow/now-loader';
import {createCustomElement} from '@servicenow/ui-core';
import {dirtyModalBehavior} from '../../behaviors/dirtyBehavior/dirtyModalBehavior';
import {getTabItems} from './utils';
import {default as modalBehaviors} from '../../behaviors/modalBehavior';
import get from 'lodash/get';
import packageJson from '../../../package.json';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './listMenu.scss';

const view = state => {
	const {
		componentId,
		properties: {
			activeTabId,
			hideListCreation,
			isEditing,
			lists,
			listMenuOpen,
			loading,
			selectedItemPath
		},
		modalProps
	} = state;
	const classes = getPanelClasses(listMenuOpen);
	const hideModal = !get(modalProps, 'type', '');
	const tabItems = getTabItems(lists);

	return (
		<div className="sn-panel-max-height">
			{!hideModal ? (
				<sn-list-menu-modal-new-list modalProps={modalProps} />
			) : null}

			<div className={classes}>
				{loading ? renderSpinnerContainer() : null}

				{[...Object.keys(lists)].map(key => {
					const props = {
						activeTabId,
						instanceId: `${componentId + '-' + key}`,
						list: lists[key],
						tabItems
					};
					if (
						!loading &&
						isEditing &&
						lists[key].canEdit &&
						activeTabId === key
					) {
						return renderEditor({...props});
					} else if (!loading && !isEditing && activeTabId === key) {
						return renderDisplay({
							...props,
							canExpand: lists[key].canExpand,
							hideListCreation,
							selectedItemPath
						});
					} else {
						null;
					}
				})}
			</div>
		</div>
	);
};

const getPanelClasses = listMenuOpen =>
	listMenuOpen ? 'sn-panel' : 'sn-panel is-hidden';

const renderEditor = props => <sn-list-menu-editor {...props} />;

const renderDisplay = props => <sn-list-menu-display {...props} />;

const renderSpinnerContainer = () => {
	return (
		<div className="sn-menu-loader">
			<now-loader size={'md'} />
		</div>
	);
};

/**
 * A presentational collapsable sidebar menu panel component to select items such as lists, with accordions for list items and tabs for different menus.
 *
 * ```jsx
 * <now-list-menu
 * 		activeTabId={...}
 * 		hideListCreation={...}
 * 		lists={...}
 * 		selectedItemPath={...} />
 * ```
 *
 * @seismicElement now-list-menu
 * @summary A collapsable sidebar menu panel component to select items such as lists, with accordions for list items and tabs for different menus.
 */
createCustomElement('now-list-menu', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		/**
		 * The key of the tab that is intended to be active.
		 * @type {string}
		 */
		activeTabId: {
			default: ''
		},
		/**
		 * Toggles the user's ability to create new lists.
		 * @type {boolean}
		 */
		hideListCreation: {
			default: false
		},
		/**
		 * A flag that indicates a user is currently editing the current list menu.
		 * @type {boolean}
		 */
		isEditing: {
			default: false
		},
		/**
		 * An object handling the list items.
		 * @type {object}
		 */
		lists: {
			default: {},
			schema: {
				type: 'object',
				properties: {
					title: {type: 'string'},
					key: {type: 'string'},
					canEdit: {type: 'boolean'},
					canExpand: {type: 'boolean'},
					canCreate: {type: 'boolean'},
					data: {
						type: 'array',
						items: {
							type: 'object',
							properties: {
								columns: {type: 'string'},
								conditions: {},
								displayValue: {type: 'string'},
								id: {type: 'string'},
								label: {type: 'string'},
								order: {type: 'string'},
								selectionPath: {type: 'array'},
								table: {type: 'string'},
								targetTable: {type: 'string'}
							}
						}
					},
					newListForm: {
						type: 'object',
						properties: {
							form: {type: 'string'},
							view: {type: 'string'}
						}
					}
				}
			}
		},
		/**
		 * Denotes whether the menu is open
		 * @type {boolean}
		 */
		listMenuOpen: {
			default: true
		},
		/**
		 * A flag that indicates whether or not the list menu should show a loading spinner.
		 * @type {boolean}
		 */
		loading: {
			default: true
		},
		/**
		 * Represent the path to the item that is currently selected from the active tab list in the menu.
		 * @type {string[]}
		 */
		selectedItemPath: {
			default: [],
			schema: {
				type: 'array',
				items: {
					type: 'string'
				}
			}
		}
	},
	styles,
	onConnect(host) {
		if (!window.listComponentRegistry) {
			window.listComponentRegistry = new WeakMap();
		}
		window.listComponentRegistry.set(host, packageJson.version);
	},
	onDisconnect(host) {
		if (window.listComponentRegistry) {
			window.listComponentRegistry.delete(host);
		}
	},
	behaviors: [modalBehaviors, dirtyModalBehavior],
	dispatches: {
		/**
		 * Fired when expanding/collapsing content tree from a list in the menu.
		 * listKey: is the componantName the expand/collapse click came from.
		 * items: is an array of string of the remaining expanded items in the list.
		 * @type {{listKey: string, items: array}}
		 */
		'NOW_LIST_MENU#EXPANDED_ITEMS_SET': {},
		/**
		 * Fired when selecting a list from the menu.
		 * @type {{item: object}}
		 */
		'NOW_LIST_MENU#MENU_ITEM_CLICKED': {},
		/**
		 * Fired after reordering lists in a menu.
		 * @type {{listModel: object}}
		 */
		'NOW_LIST_MENU#UPDATE_LIST_ITEM_ORDER_REQUEST': {},
		/**
		 * Fired after dragging list item in menu while editing/reordering.
		 * @type {{listKey: string, isDragging: boolean}}
		 */
		'NOW_LIST_MENU#LIST_EDIT_ITEM_DRAGGED': {}
	}
});
