import '@devsnc/sn-drag-and-drop';
import '@servicenow/now-tabs';
import {
	EDIT_ITEM_DRAGGED,
	MESSAGES,
	REORDER_LIST,
	UPDATE_LIST_ORDER
} from '../../constants';
import {createCustomElement} from '@servicenow/ui-core';
import {t} from 'sn-translate';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from '../listMenuDisplay/listMenuDisplay.scss';

const doneButtonLabel = t('Done');

const view = (state, {dispatch}) => {
	const {
		properties: {list, instanceId, activeTabId, tabItems}
	} = state;

	const onDataChange = newData => {
		dispatch(UPDATE_LIST_ORDER, {
			listModel: {
				...list,
				data: newData
			},
			isEditing: true
		});
	};

	const noop = () => {};

	const dragAndDropState = dndState => {
		noop(dndState);
		dispatch(EDIT_ITEM_DRAGGED, {
			listKey: activeTabId,
			...dndState
		});
	};

	return (
		<div className="sn-list-menu">
			<now-tabs
				items={tabItems}
				fixed-width
				manage-selected-item={true}
				selected-item={activeTabId}
			/>
			<div className="sn-list-menu-body">
				<sn-drag-and-drop
					componentId={`${instanceId}_list_editor_my_lists`}
					dragAndDropState={dragAndDropState}
					emptyStateMessage={MESSAGES.MY_LIST.EMPTY_STATE_CONTENT}
					emptyStateMessageHead={MESSAGES.MY_LIST.EMPTY_STATE_TITLE}
					id={activeTabId}
					key={activeTabId}
					options={list}
					onDataChange={onDataChange}
				/>
			</div>
			<div className="sn-list-menu-footer editor">
				<now-button
					aria-label={doneButtonLabel}
					variant="secondary"
					size="md"
					label={doneButtonLabel}
					key={`doneButton`}
					data-key={`doneButton`}
					on-click={() => {
						dispatch(REORDER_LIST, {isEditing: false});
					}}
				/>
			</div>
		</div>
	);
};

createCustomElement('sn-list-menu-editor', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		activeTabId: {
			default: ''
		},
		instanceId: {
			default: ''
		},
		list: {
			default: {},
			schema: {
				type: 'object',
				properties: {
					title: {type: 'string'},
					key: {type: 'string'},
					canEdit: {type: 'boolean'},
					canExpand: {type: 'boolean'},
					canCreate: {type: 'boolean'},
					data: {
						type: 'array',
						items: {
							type: 'object',
							properties: {
								columns: {type: 'string'},
								conditions: {},
								displayValue: {type: 'string'},
								id: {type: 'string'},
								label: {type: 'string'},
								order: {},
								selectionPath: {type: 'array'},
								table: {type: 'string'},
								targetTable: {type: 'string'}
							}
						}
					},
					newListForm: {
						type: 'object',
						properties: {
							form: {type: 'string'},
							view: {type: 'string'}
						}
					}
				}
			}
		},
		tabItems: {
			default: [],
			type: 'array',
			items: {
				type: 'object',
				properties: {
					id: {type: 'string'},
					label: {type: 'string'}
				}
			}
		}
	},
	styles
});
