import {CLOSE_MODAL, OPEN_MODAL} from '../constants';
import {getAndClearRetainedFocus} from '../utils/focusUtility';
import isEmpty from 'lodash/isEmpty';

const modalBehavior = {
	name: 'modalBehavior',
	properties: {},
	initialState: {
		modalProps: {
			type: ''
		}
	},
	actionHandlers: {
		[OPEN_MODAL]: {
			effect: ({action, updateState}) => {
				updateState({
					modalProps: {
						...action.payload.modalProps
					}
				});
			},
			stopPropagation: true
		},
		[CLOSE_MODAL]: {
			effect: ({updateState, dispatch, action}) => {
				const {
					payload: {actionToDispatch}
				} = action;
				updateState({
					modalProps: {
						type: ''
					}
				});
				if (!isEmpty(actionToDispatch)) dispatch(actionToDispatch);
			},
			interceptors: [
				{
					after: () => {
						getAndClearRetainedFocus('newListButton');
					}
				}
			],
			stopPropagation: true
		}
	}
};

export default modalBehavior;
