import '@devsnc/sn-glide-form-controls';
import '@servicenow/now-modal';
import '@servicenow/now-tabs';
import {
	CLOSE_MODAL,
	FETCH_LISTS,
	IFRAME_EVENT,
	MODAL_ACTIONS,
	NOW_TABS_SELECTED_SET,
	TABLE_SELECTED_SET
} from '../../constants';
import {
	COMPONENT_BOOTSTRAPPED,
	COMPONENT_DISCONNECTED
} from '@seismic/core/lib/constants';
import {createCustomElement} from '@seismic/core';
import {t} from 'sn-translate';
import isEmpty from 'lodash/isEmpty';
import snabbdom from '@seismic/snabbdom-renderer';
import styles from './newListModal.scss';

const newListModalTitle = t('New List');
const newListModalSubtitle = t(
	'You can create a list from an existing list or create a brand new list.'
);
const createFromExistingID = 'create-from-existing';
const createFromExistingLabel = t('Create from an existing list');
const createNewID = 'create-new';
const createNewLabel = t('Create new');

let eventListenerRef;

const iframeEventEffect = coeffects => {
	const {dispatch} = coeffects;
	dispatch(CLOSE_MODAL, {
		actionToDispatch: FETCH_LISTS
	});
};

const messageHandler = dispatch => {
	return e => {
		const {messageType, modalAction, data} = e.data;
		if (
			messageType === 'IFRAME_MODAL_MESSAGE_TYPE' &&
			[
				'IFRAME_MODAL_ACTION_CONFIRMED',
				'IFRAME_MODAL_ACTION_CANCELED'
			].includes(modalAction)
		) {
			window.removeEventListener('message', eventListenerRef);
			eventListenerRef = null;
			dispatch(IFRAME_EVENT, {data});
		}
	};
};

const getIframeURL = newListForm => {
	const {form, view} = newListForm;
	return `${form + '?'}${(isEmpty(view) ? '' : 'sysparm_view=' + view + '&') +
		'sysparm_titleless=true&sysparm_skipmsgs=true&sys_id=-1'}`;
};

const view = (state, {dispatch}) => {
	const {
		properties: {
			modalProps: {useiFrame, newListForm}
		},
		activeTabId
	} = state;
	let iFrameRef, iFramePathName;
	let initialized = false;
	const iFrameSrc = getIframeURL(newListForm);

	let modalContent = () => {
		if (!useiFrame) {
			return (
				<div>
					<h4>{newListModalSubtitle}</h4>
					<now-tabs
						items={[
							{
								id: createFromExistingID,
								label: createFromExistingLabel
							},
							{
								id: createNewID,
								label: createNewLabel
							}
						]}></now-tabs>
					{renderModalBody()}
				</div>
			);
		}
		return (
			<iframe
				src={iFrameSrc}
				sandbox="allow-same-origin allow-forms allow-scripts allow-popups-to-escape-sandbox allow-popups allow-modals"
				className={`sn-modal-iframe`}
				hook-insert={vnode => {
					iFrameRef = vnode.elm;
					iFramePathName = iFrameRef.contentWindow.location.pathname;
					iFrameRef.addEventListener('load', iFrameOnLoad);
				}}
				hook-destroy={() => {
					if (iFrameRef) iFrameRef.removeEventListener('load', iFrameOnLoad);
				}}
			/>
		);
	};
	const iFrameOnLoad = () => {
		if (!initialized) {
			initialized = true;
			iFramePathName = iFrameRef.contentWindow.location.pathname;
		} else if (iFrameRef.contentWindow.location.pathname !== iFramePathName) {
			dispatch(CLOSE_MODAL, {
				actionToDispatch: FETCH_LISTS
			});
		}
	};

	const renderModalBody = () => {
		if (activeTabId === createFromExistingID) {
			return <div className="sn-new-list-modal-body">tab1</div>;
		}
		return (
			<div className="sn-new-list-modal-body">
				<sn-record-tablename-connected
					name="create-new-list-select-table"
					label={t('Select Table')}
					placeholder={t('Select Table...')}
					onValueChange={({value}) => {
						if (value) dispatch(TABLE_SELECTED_SET, {value});
					}}
					className="sn-modal-field"
					dictionaryAttributes={{}}
				/>
			</div>
		);
	};

	return (
		<now-modal
			size="lg"
			header-label={newListModalTitle}
			manage-opened
			opened={true}>
			{modalContent()}
		</now-modal>
	);
};

createCustomElement('sn-list-menu-modal-new-list', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		modalProps: {}
	},
	initialState: {
		activeTabId: createFromExistingID,
		selectedTable: ''
	},
	actionHandlers: {
		[MODAL_ACTIONS.FOOTER_ACTION_CLICKED]: {
			effect: iframeEventEffect,
			stopPropagation: true
		},
		[MODAL_ACTIONS.OPENED_SET]: {
			effect: iframeEventEffect,
			stopPropagation: true
		},
		[IFRAME_EVENT]: {
			effect: iframeEventEffect,
			stopPropagation: true
		},
		[COMPONENT_BOOTSTRAPPED]: {
			effect: coeffects => {
				const {dispatch} = coeffects;
				eventListenerRef = messageHandler(dispatch);
				window.addEventListener('message', eventListenerRef);
			},
			stopPropagation: true
		},
		[COMPONENT_DISCONNECTED]: {
			effect: () => {
				if (eventListenerRef)
					window.removeEventListener('message', eventListenerRef);
			}
		},
		[NOW_TABS_SELECTED_SET]: {
			effect: ({
				action: {
					payload: {value}
				},
				updateState
			}) => {
				updateState({activeTabId: value});
			},
			stopPropagation: true
		},
		[TABLE_SELECTED_SET]: {
			effect: ({
				action: {
					payload: {value}
				},
				updateState
			}) => {
				updateState({selectedTable: value});
			},
			stopPropagation: true
		}
	},
	styles
});
