import isEmpty from 'lodash/isEmpty';

export const getTabItems = items => {
	let tabItems = [];
	if (isEmpty(items)) return tabItems;

	for (let [key, value] of Object.entries(items)) {
		tabItems.push({id: key, label: value.title});
	}
	return tabItems;
};

export const getExpandedItems = ({
	list = {},
	expandedItems = [],
	hasUserCollapsedAllItems = false
}) => {
	if (isEmpty(list)) return [];
	let expandedMenuItems = [];

	if (isEmpty(expandedItems) && !hasUserCollapsedAllItems) {
		if (!isEmpty(list)) {
			list.data.forEach(item => {
				if (item.isExpanded) {
					expandedMenuItems.push([item.id]);
				}
			});
		}
	} else {
		expandedMenuItems = expandedItems;
	}
	return expandedMenuItems;
};
