/**
 * Factory function for logging.
 *
 * import {Logger} from '../src/utils/logger';
 */
const LOG_STATUS_KEY = '__LIST_MENU_LOG_LEVEL';

const LOG_LEVEL = {
	LOG_LEVEL_ALL: 0,
	LOG_LEVEL_DEBUG: 1,
	LOG_LEVEL_LOG: 2,
	LOG_LEVEL_INFO: 2,
	LOG_LEVEL_WARN: 3,
	LOG_LEVEL_ERROR: 4
};
const {
	LOG_LEVEL_ALL,
	LOG_LEVEL_DEBUG,
	LOG_LEVEL_LOG,
	LOG_LEVEL_INFO,
	LOG_LEVEL_WARN,
	LOG_LEVEL_ERROR
} = LOG_LEVEL;

export const Logger = () => {
	// set default logging level
	if (typeof window[LOG_STATUS_KEY] === 'undefined') {
		window[LOG_STATUS_KEY] = LOG_LEVEL_ERROR;
	}

	/**
	 * Simple logging utilty to include context
	 * @usage Logger().enable();
	 *
	 * @param logLevel
	 */
	const enable = (logLevel = LOG_LEVEL_ALL) => {
		switch (logLevel) {
			default:
				logLevel = LOG_LEVEL_ERROR;
			// falls through
			case LOG_LEVEL_ALL:
			case LOG_LEVEL_DEBUG:
			case LOG_LEVEL_LOG:
			case LOG_LEVEL_INFO:
			case LOG_LEVEL_WARN:
			case LOG_LEVEL_ERROR:
				window[LOG_STATUS_KEY] = logLevel;
				break;
		}
	};

	/**
	 * @usage Logger().disable();
	 */
	const disable = () => {
		window[LOG_STATUS_KEY] = LOG_LEVEL_ERROR;
	};

	/**
	 * @usage const LOG = Logger().createLog('ComponentName');
	 *
	 * @param name
	 * @return {{log, debug, warn, error}}
	 */
	const createLog = name => {
		const _log = (type, level, args) => {
			if (level < window[LOG_STATUS_KEY]) {
				return;
			}
			args = Array.prototype.splice.call(args, 0);
			args.unshift(`(${name})`);
			// eslint-disable-next-line no-console
			console[type].apply(console, args);
		};

		return {
			debug: function() {
				_log('debug', LOG_LEVEL_DEBUG, arguments);
			},
			log: function() {
				_log('log', LOG_LEVEL_LOG, arguments);
			},
			info: function() {
				_log('info', LOG_LEVEL_INFO, arguments);
			},
			warn: function() {
				_log('warn', LOG_LEVEL_WARN, arguments);
			},
			error: function() {
				_log('error', LOG_LEVEL_ERROR, arguments);
			}
		};
	};

	return {enable, disable, createLog};
};
