import {Logger} from './logger';
import flatten from 'lodash/flatten';
import isBoolean from 'lodash/isBoolean';
import isEmpty from 'lodash/isEmpty';

// -----------------------------------------------------------------------------
// Constants
// -----------------------------------------------------------------------------

// Selectors to get common tabbable elements.
export const selector = [
	'input',
	'select',
	'textarea',
	'a[href]',
	'button',
	'[tabindex]',
	'audio[controls]',
	'video[controls]',
	'[component-id]',
	'[contenteditable]:not([contenteditable="false"])'
].join();

export const UPDATE_FOCUS_TRAP = 'UPDATE_FOCUS_TRAP';

// -----------------------------------------------------------------------------
// Shadow Dom Focus Utilities
// -----------------------------------------------------------------------------

export const findFirstTabbableElement = (root = document) => {
	/* findFirstTabbableElement
	 * Parameters:
	 *  root:
	 *    Expected Type: Node, Document, ShadowRoot
	 *    Optional: Defaults to document.
	 * Return Type: Node
	 * Description:
	 *  Finds the first tabbable element in an element or component.
	 */
	return findTabbableElement(true, root);
};

export const findLastTabbableElement = (root = document) => {
	/* findLastTabbableElement
	 * Parameters:
	 *  root:
	 *    Expected Type: Node, Document, ShadowRoot
	 *    Optional: Defaults to document.
	 * Return Type: Node
	 * Description:
	 *  Finds the last tabbable element in an element or component.
	 */
	return findTabbableElement(false, root);
};

export const findTabbableElement = (findFirst = true, root = document) => {
	// Return Type: Node
	// Finds the first or last tabbable element in an element or component.
	if (!root) return false;

	if (root.shadowRoot) root = root.shadowRoot;

	let tabbies = [...root.querySelectorAll(selector)];
	if (!findFirst) tabbies = tabbies.reverse();

	let index = 0;
	let result = undefined;

	// TODO: Maybe change code style here
	while (!result && index < tabbies.length) {
		const tabby = tabbies[index++];
		const possibleResult = tabby.shadowRoot
			? findTabbableElement(findFirst, tabby.shadowRoot)
			: tabby;
		if (possibleResult && isTabbable(possibleResult)) result = possibleResult;
	}
	return result;
};

export const isTabbable = node => {
	// Return Type: Boolean
	// Determines if an element is tabbable based on criteria that can't be done
	// with QuerySelectorAll.

	if (!node) return false;

	if (node.disabled) return false;

	if (node.offsetParent === null) return false;

	if (getComputedStyle(node).visibility === 'hidden') return false;

	if (node.tagName === 'INPUT' && node.type === 'hidden') return false;

	if (getTabIndex(node) === -1) return false;

	return true;
};

export const getTabIndex = node => {
	// Return Type: Number
	// Gets the numeric tab index of an element.

	const tabIndex = parseInt(node.getAttribute('tabindex'), 10);
	if (!isNaN(tabIndex)) return tabIndex;
	return node.contentEditable === 'true' ? 0 : node.tabIndex;
};

export const findAllTabbableElements = (root = document) => {
	// Return Type: Array[Nodes]
	// Finds all the tabbable elements in an element or component.

	if (root.shadowRoot) root = root.shadowRoot;
	const tabbies = root.querySelectorAll(selector);
	let expandedTabbies = [];
	tabbies.forEach(tabby => {
		if (!tabby.shadowRoot && isTabbable(tabby)) {
			expandedTabbies.push(tabby);
		} else {
			expandedTabbies.push(findAllTabbableElements(tabby));
		}
	});
	return flatten(expandedTabbies);
};

export const shadowQuerySelectorAll = (root = document, selector) => {
	// Return Type: Array[Nodes]
	// Finds all elements across shadow roots

	if (!selector) return [];

	if (root.shadowRoot) root = root.shadowRoot;
	const nodes = root.querySelectorAll(selector);
	let expandedNodes = [];
	nodes.forEach(node => {
		if (!node.shadowRoot) {
			expandedNodes.push(node);
		} else {
			expandedNodes.push(shadowQuerySelectorAll(node));
		}
	});
	return flatten(expandedNodes);
};

export const findActiveElement = (root = document) => {
	/* findActiveElement
	 * Parameters:
	 *  root:
	 *    Expected Type: Node, Document, ShadowRoot
	 *    Optional: Defaults to document.
	 * Return Type: Node
	 * Description:
	 *  Finds the active element of the page.
	 */
	const activeElement = root.activeElement;
	if (activeElement && activeElement.shadowRoot) {
		return findActiveElement(activeElement.shadowRoot) || activeElement;
	}
	return activeElement;
};

export const elementHasActiveElement = (node, root = document) => {
	/* WARNING: Possible issue in IE11, appears to give false positives at times.
	 * As of now-record-list@8.0.21 this function is not in active use, and the
	 * only function that might call it (focusWithinClassManager) will not get to
	 * call it as currently used (see GridHeaderRowCell and GridRowCell).
	 */

	/* findActiveElement
	 * Parameters:
	 *  node:
	 *    Expected Type: Node
	 *  root:
	 *    Expected Type: Node, Document, ShadowRoot
	 *    Optional: Defaults to document.
	 * Return Type: Boolean
	 * Description:
	 *  Determines if an element is or contains the active element.
	 */
	const activeElement = root.activeElement;
	if (!activeElement) return false;

	if (node == activeElement || node.contains(activeElement)) return true;

	if (activeElement.shadowRoot)
		return elementHasActiveElement(node, activeElement.shadowRoot);

	return false;
};

export const focusWithinClassManager = (
	container,
	className,
	focusIsWithin
) => {
	/* focusWithinClassManager
	 * Parameters:
	 *  container:
	 *    Expected Type: Node
	 * 	className:
	 *		Expected Type: String
	 * 	focusIsWithin:
	 *		Expected Type: Boolean
	 * 		Optional: Defaults to looking with elementHasActiveElement.
	 * Return Type: Boolean
	 * Description:
	 *  Adds a CSS class to the container element if either focusIsWithin is true
	 *  or focusIsWithin is undefined and elementHasActiveElement returns true.
	 *  Removes that CSS class if focusIsWithin is false or focusIsWithin is
	 *  undefined and elementHasActiveElement returns false.
	 */

	if (!container || !className) return false;

	const addClass = isBoolean(focusIsWithin)
		? focusIsWithin
		: elementHasActiveElement(container);

	if (addClass) container.classList.add(className);
	else container.classList.remove(className);

	return true;
};

// -----------------------------------------------------------------------------
// Element Retainer: Remembers elements for manipulation
// -----------------------------------------------------------------------------

const elementRetainer = new Map();

export const addRetainedElement = (key, node = findActiveElement()) => {
	/* addRetainedElement
	 * Parameters:
	 *  key:
	 *    Expected Type: Any, usually String
	 *  node:
	 *    Expected Type: Node
	 *    Optional: Defaults to result of findActiveElement().
	 * Return Type: Boolean
	 * Description:
	 *  Sets an element to later return focus to. Returns true on successfully
	 *  setting the element. Returns false if no active element is given/found,
	 *  or if the attempt to set fails.
	 */
	if (!node) return false;
	return elementRetainer.set(key, node);
};

export const removeRetainedElement = key => {
	/* getAndClearRetainedFocus
	 * Parameters:
	 *  key:
	 *    Expected Type: Any, usually String
	 * Return Type: Boolean
	 * Description:
	 *  Focuses on a previously stored element and removes it from storage.
	 *  Returns true on successfully focusing on the element and deleting the
	 * entry. Returns false if no element was found or deletion failed.
	 */
	return elementRetainer.delete(key);
};

export const updateRetainedElement = (key, node = findActiveElement()) => {
	/* updateRetainedElement
	 * Parameters:
	 *  key:
	 *    Expected Type: Any, usually String
	 *  node:
	 *    Expected Type: Node
	 *    Optional: Defaults to result of findActiveElement().
	 * Return Type: Boolean
	 * Description:
	 *  Removes the previously stored element without focusing on it.
	 *  Sets the current active element as the new stored element. Returns the
	 *  result of addRetainedElement.
	 */
	removeRetainedElement(key);
	return addRetainedElement(key, node);
};

export const getAndClearRetainedFocus = key => {
	/* getAndClearRetainedFocus
	 * Parameters:
	 *  key:
	 *    Expected Type: Any, usually String
	 * Return Type: Boolean
	 * Description:
	 *  Focuses on a previously stored element and removes it from storage.
	 *  Returns true on successfully focusing on the element and deleting the
	 * entry. Returns false if no element was found or deletion failed.
	 */
	const focusThis = elementRetainer.get(key);
	if (!focusThis) return false;
	focusThis.focus();
	return elementRetainer.delete(key);
};

export const getAndResetRetainedScroll = (key, scrollToBottom) => {
	/* getAndResetRetainedScroll
	 * Parameters:
	 *  key:
	 *    Expected Type: Any, usually String
	 *  scrollToBottom (optional)
	 *    Expected Type: Boolean
	 * 		Scrolls to bottom if truthy, scrolls to top otherwise
	 * Return Type: Boolean
	 * Description:
	 *  Resets scroll on a previously stored element.
	 *  Can scroll to top or to the bottom, based on scrollToBottom
	 *  Returns true on successfully finding scroll on the element
	 *  Returns false if no element was found.
	 */
	const element = elementRetainer.get(key);

	if (!element) return false;

	if (scrollToBottom) {
		element.scrollTop = element.firstChild.scrollHeight;
		element.scrollLeft = 0;
		return true;
	} else {
		element.scrollTop = 0;
		element.scrollLeft = 0;
		return true;
	}
};

// -----------------------------------------------------------------------------
// Focus Trap: Keeps the user from tabbing out of an element.
// -----------------------------------------------------------------------------

const focusTrap = new Map();

const defaultConfig = {
	clickOutsideDeactivates: false, // for future
	moveFocusInsideTrap: true,
	firstElement: null,
	lastElement: null
};

export const activateFocusTrap = (id, node, config = {}) => {
	/* activateFocusTrap
	 * Parameters:
	 *  id:
	 *    Expected Type: Any, usually String
	 *  node:
	 *    Expected Type: Node, Document, ShadowRoot
	 *  config:
	 *    Expected Type: Object
	 *    Optional: Defaults to empty object, to be merged with defaultConfig.
	 * Return Type: Boolean
	 * Description:
	 *  Initalizes a simple focus trap. Returns true on successful initialization
	 *  of the trap. Returns false if no tabbable elements are found in node or
	 *  storing the entry fails. Deactivates an existing focus trap if one already
	 *  exists for the given id. Moves focus to the first tabby by default. Sets
	 *  keydown event listeners on the first and last tabbies. When tab is pressed
	 *  on the first Tabby element, it moves focus to the last Tabby element. When
	 *  shift tab is pressed on the last it focuses the first.
	 *  The config object can be given the elements to set as the first and last
	 *  tabbies.
	 */

	deactivateFocusTrap(id);
	config = {...defaultConfig, ...config};
	const {moveFocusInsideTrap, firstElement, lastElement} = config;
	const LOG = Logger().createLog('focusUtility');
	const firstTabby = firstElement || findFirstTabbableElement(node);
	const lastTabby = lastElement || findLastTabbableElement(node);

	if (!firstTabby) {
		LOG.warn(
			'activateFocusTrap canceled: No tabbable elements found within the element.'
		);
		return false;
	}

	if (moveFocusInsideTrap && !elementHasActiveElement(node)) firstTabby.focus();

	const firstKeydown = evt => {
		const {key, shiftKey} = evt;
		if (key === 'Tab' && shiftKey) {
			evt.preventDefault();
			lastTabby.focus();
		}
	};

	const lastKeydown = evt => {
		const {key, shiftKey} = evt;
		if (key === 'Tab' && !shiftKey) {
			evt.preventDefault();
			firstTabby.focus();
		}
	};

	firstTabby.addEventListener('keydown', firstKeydown);
	lastTabby.addEventListener('keydown', lastKeydown);

	const tabbyTrapEntry = {
		config,
		node,
		firstTabby,
		lastTabby,
		firstKeydown,
		lastKeydown
	};

	return focusTrap.set(id, tabbyTrapEntry);
};

export const deactivateFocusTrap = id => {
	/* deactivateFocusTrap
	 * Parameters:
	 *  id:
	 *    Expected Type: Any, usually String
	 * Return Type: Boolean
	 * Description:
	 *  Removes event listeners from the tabbies and removes the entry from the
	 *  focus trap storage. Returns true on successful removal from storage.
	 *  Returns false if retrievall or removal from storage fails.
	 */

	const tabbyTrapEntry = focusTrap.get(id);

	if (isEmpty(tabbyTrapEntry)) return false;

	const {lastKeydown, firstKeydown, firstTabby, lastTabby} = tabbyTrapEntry;
	firstTabby.removeEventListener('keydown', firstKeydown);
	lastTabby.removeEventListener('keydown', lastKeydown);

	return focusTrap.delete(id);
};

export const updateFocusTrap = id => {
	/* updateFocusTrap
	 * Parameters:
	 *  id:
	 *    Expected Type: Any, usually String
	 * Return Type: Boolean
	 * Description:
	 *  If a focus trap exists for the given id, it reactivates it to update the
	 *  tabbies without moving focus. Returns true if activateFocusTrap succeeds.
	 *  Returns false if no entry is found for the given id or if
	 *  activateFocusTrap fails.
	 */
	const tabbyTrapEntry = focusTrap.get(id);
	if (isEmpty(tabbyTrapEntry)) return false;

	const {node, config = {}} = tabbyTrapEntry;
	if (isEmpty(node)) return false;

	return activateFocusTrap(id, node, {
		...defaultConfig,
		...config
	});
};
